@font-face {
  font-family: "PT Sans Narrow";
  src: url("../fonts/pt-sans-narrow.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans Narrow";
  src: url("../fonts/pt-sans-narrow-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

h2 {
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
}

.btn {
  display: block;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: #000;
  border: 1px solid #e5ddd5;
}
.btn:not(:disabled):hover {
  border-color: #d5cabe;
}
.btn:active {
  background-color: #d5c9bd;
  color: rgba(0, 0, 0, 0.3);
}
.btn:active svg {
  fill: rgba(0, 0, 0, 0.3);
}
.btn:disabled svg {
  fill: rgba(0, 0, 0, 0.3);
}

.btn--dark {
  color: #fff;
  border-color: #3e3e3e;
}
.btn--dark:not(:disabled):hover {
  border-color: #515151;
}
.btn--dark:active {
  background-color: #252525;
  color: rgba(255, 255, 255, 0.3);
}

.wrapper {
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 768px) {
  body {
    background-color: #353535;
  }
  h2 {
    font-size: 40px;
    text-align: left;
  }
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
