.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.slider__item {
  display: none;
}

.slider__item--active {
  display: block;
}

.slider__toggles {
  display: flex;
  justify-content: center;
}

.slider__toggle {
  display: block;
  padding: 0;
  width: 10px;
  height: 10px;
  font-size: 0;
  background: none;
  border: 1px solid #a5a5a5;
  transform: rotate(45deg);
}

.slider__toggle:not(:last-child) {
