  .appointment-form__addition-services {
    border-top: 1px solid #e5ddd5;
  }
  .appointment-form__beard-choise {
    justify-content: space-around;
    margin-bottom: 60px;
    margin-top: 60px;
  }
  .appointment-form__addition-services {
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 60px;
  }
  .appointment-form__addition-service {
    width: calc(50% - 1px);
  }
}
@media (min-width: 1200px) {
  .appointment-form__passport-data {
    justify-content: space-between;
  }
  .appointment-form__passport-data input {
    width: calc((100% - 74px) / 3) !important;
  }
  .appointment-form__passport-data input:nth-child(2) {
    border-left: 1px solid #e5ddd5;
    border-right: 1px solid #e5ddd5;
  }
  .appointment-form__contacts {
