.modal {
  display: none;
}

.modal--open {
  display: block;
}

.modal__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1000;
}

.modal__login {
  position: absolute;
  top: 135px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 30px 50px 0 rgba(0, 0, 0, 0.75);
  z-index: 1001;
}

@media (min-width: 768px) {
  .modal__login {
    top: 128px;
  }
}
.login-form {
  padding: 32px 20px 37px;
  background-color: #f8f5f2;
  color: #000;
}

.login-form__title {
  margin: 0 0 25px;
}

.login-form p {
  margin: 25px 0;
  text-align: center;
}

.login-form__input-wrapper {
  position: relative;
  display: block;
  height: auto;
  margin-bottom: 6px;
}
.login-form__input-wrapper input {
  width: 280px;
  padding: 15px;
  color: #000;
  background-color: #fff;
  border: 1px solid #e5ddd5;
}
.login-form__input-wrapper input::placeholder {
  color: #000;
}
.login-form__input-wrapper input:hover {
  border-color: #d5c9bd;
}
.login-form__input-wrapper input:focus {
  outline: none;
  border-color: #999999;
}
.login-form__input-wrapper svg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 19px;
}

.login-form__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 26px;
}
.login-form__row [type=checkbox] {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}
.login-form__row label {
  position: relative;
  padding-left: 43px;
  cursor: pointer;
  line-height: 33px;
  user-select: none;
}
.login-form__row label::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 33px;
  height: 33px;
  border: 1px solid #e5ddd5;
  background-color: #fff;
}
.login-form__row :checked + label::after {
  content: "";
  position: absolute;
  top: 9px;
  left: 7px;
  width: 22px;
  height: 16px;
  background-image: url("../images/sprite.svg#checkbox");
}
.login-form__row :not(:disabled) + label:hover::before {
  border-color: #d5c9bd;
}
.login-form__row :not(:disabled) + label:active::before {
  background-color: #d5c9bd;
}

.login-form__forgot {
  color: inherit;
}

.login-form__submit {
  width: 130px;
  color: #fff;
  background-color: #826550;
}
.login-form__submit:not(:disabled):hover, .login-form__submit:active {
  background-color: #6a4d38 !important;
}
.login-form__submit:active {
  color: rgba(255, 255, 255, 0.3);
}
.login-form__submit:disabled {
  background-color: #aeaeae;
}

.login-form__close {
  width: 130px;
}

@media (min-width: 768px) {
  .login-form {
    padding: 50px 70px 63px;
  }
  .login-form__title {
    text-align: center;
  }
  .login-form__input-wrapper input {
    width: 320px;
    padding: 23px 25px;
    font-size: 24px;
  }
  .login-form__submit {
    width: 100%;
  }
  .login-form__close {
    position: absolute;
    top: 62px;
    right: -95px;
    width: 26px;
    height: 26px;
    padding: 0;
    font-size: 0;
    background-color: transparent;
    border: none;
  }
  .login-form__close::before, .login-form__close::after {
    content: "";
    position: absolute;
    top: 10px;
    left: -5px;
    width: 36px;
    height: 2px;
    background-color: #fff;
  }
  .login-form__close::before {
    transform: rotate(45deg);
  }
  .login-form__close::after {
    transform: rotate(-45deg);
  }
  .login-form__close:hover {
    background-color: transparent;
  }
  .login-form__close:hover::before, .login-form__close:hover::after {
    background-color: #826550;
  }
  .login-form__close:active {
    background-color: transparent;
  }
  .login-form__close:active::before, .login-form__close:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
}