@use "../base/variables" as *;

.contacts {
  font-size: 18px;
  line-height: 27px;
  padding: 0 20px;
}

.contacts__title {
  font-weight: bold;
}

.contacts__phone {
  display: block;
  padding: 15px 30px;
  margin-top: 20px;
  color: inherit;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  border: 1px solid #3e3e3e;

  &:hover {
    border-color: #515151;
  }

  &:active {
    color: rgba(#fff, 0.3);
  }
}

.contacts__phone-mobile {
  display: none;
}

@media (min-width: $tablet-width) {
  .contacts {
    text-align: left;
    padding: 0;
  }

  .contacts__title {
    text-transform: uppercase;
  }

  .contacts__text {
    margin-top: 0.5em;
  }

  .contacts__text br {
    display: none;
  }

  .contacts__phone {
    display: inline;
    padding: 0;
    margin: 0;
    font-weight: normal;
    border: none;

    &:hover {
      text-decoration: underline;
    }

    &:active {
      opacity: 0.6;
    }
  }

  .contacts__phone-mobile {
    display: inline;
  }
}
