@use "../base/variables" as *;

.copyright {
  padding: 0 20px;
}

.copyright__text {
  font-weight: bold;
}

.copyright__link {
  margin-top: 10px;
}

@media (min-width: $tablet-width) {
  .copyright {
    padding: 0;
  }

  .copyright__text {
    font-weight: normal;
  }

  .copyright__link {
    padding-left: 0;
    padding-right: 0;
  }
}
