@use "../base/variables" as *;

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

h2 {
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
}

.btn {
  display: block;
  padding: 15px 30px;

  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: #000;

  border: 1px solid #e5ddd5;

  &:not(:disabled):hover {
    border-color: #d5cabe;
  }

  &:active {
    background-color: #d5c9bd;
    color: rgba(#000, 0.3);

    svg {
      fill: rgba(#000, 0.3);
    }
  }

  &:disabled {
    svg {
      fill: rgba(#000, 0.3);
    }
  }
}

.btn--dark {
  color: #fff;

  border-color: #3e3e3e;

  &:not(:disabled):hover {
    border-color: #515151;
  }

  &:active {
    background-color: #252525;
    color: rgba(#fff, 0.3);
  }
}

.wrapper {
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: $tablet-width) {
  body {
    background-color: #353535;
  }

  h2 {
    font-size: 40px;
    text-align: left;
  }
}
